<template>
  <v-app>
    <v-navigation-drawer persistent v-model='drawer' fixed app class='primary--text pa-2'>
      <v-card hover class='px-2 pt-2'>
        <v-combobox v-model='ori' :items='airportName' class='my-2' label='Select an origin' />
        <v-combobox v-model='des' :items='airportName' class='my-2' label='Select a destination' />
      </v-card>
      <v-card hover class='pa-2 subheading'>
        <div>- tau: {{ tauSum }}</div>
        <div v-for='(x, i) in route' :key='i'>
          <h3>{{ i + 1 }}</h3>
          - hub: {{ x.h[0] || desFil + '(direct)' }}{{ x.h[1] ? ' -> ' + x.h[1] : ''  }}<br>
          - isMajor: {{ x.m ? 'yes' : 'no' }}<br>
          - tau: {{ x.tau }}<br>
          - ratio: {{ round(x.tau / tauSum * 100, 2) }}%
          <v-divider class='my-2' />
        </div>
      </v-card>
    </v-navigation-drawer>
    <v-toolbar app class='primary' dark>
      <v-toolbar-title>DB1B Flight Routes Visualization</v-toolbar-title>
      <v-spacer />
    </v-toolbar>
    <v-content>
      <Visualizer :ori='oriFil' :des='desFil' :airport='airport' :route='route' />
    </v-content>
  </v-app>
</template>


<script>
import Visualizer from './components/Visualizer'
import airport    from './shared/airport'

export default {
  data: () => ({
    drawer : true,
    ori    : '',
    des    : '',
    airport,
    airportName: _.map(airport, n => `${ n.code }: ${ n.city }`),
  }),
  computed: {
    oriFil() {
      return _.join(_.slice(this.ori, 0, 3), '')
    },
    desFil() {
      return _.join(_.slice(this.des, 0, 3), '')
    },
    route() {
      const ix = _.findIndex(airport, n => n.code === this.oriFil)
      const jx = _.findIndex(airport, n => n.code === this.desFil)
      return chartData[ix][jx][0]
    },
    tauSum() {
      return _.sumBy(this.route, 'tau')
    },
  },
  methods: {
    round(x, i) {
      return _.round(x, i)
    }
  },
  created() {
    this.$vuetify.theme.primary = '#7E57C2',
    this.ori = this.airportName[0]
    this.des = this.airportName[1]
  },
  components: {
    Visualizer
  },
}
</script>
