<template>
  <v-layout class='pa-0 ma-0'>
    <div id='echartsWrapper' />
  </v-layout>
</template>

<script>
import echarts   from 'echarts'
import usa       from '../shared/usa.fine'

export default {
  props: ['ori', 'des', 'airport', 'route'],
  data: () => ({
    chart: '',
  }),
  computed: {
  },
  methods: {
    drawChart() {
      this.chart = echarts.init($('#echartsWrapper'))

      const planePath = 'path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z'

      const effect = { show: true, period: 6, trailLength: 0.7, color: '#fff', symbolSize: 3 }


      const convertData = (y, x) => {
        const t = x || this.ori
        const from = this.getCoords(t)
        const to   = this.getCoords(y)
        return [{
          fromName: t,
          toName  : y,
          coords  : [from, to],
        }]
      }

      const color  = ['#86F948', '#ffa022', '#46bee9','#86F948', '#ffa022', '#46bee9','#86F948', '#ffa022', '#46bee9']
      const series = [{
        name: this.ori,
        type: 'effectScatter',
        coordinateSystem: 'geo',
        zlevel: 0,
        rippleEffect: {
          brushType: 'stroke'
        },
        label: {
          normal: {
            show: true,
            position: 'right',
            formatter: '{b}'
          }
        },
        symbolSize(val) {
          return val[2] / 4
        },
        itemStyle: {
          normal: {
            color: '#B39DDB'
          }
        },
        data: [{
          name : this.ori,
          value: _.concat(this.getCoords(this.ori), 100)
        }]
      }, {
        name: this.des,
        type: 'effectScatter',
        coordinateSystem: 'geo',
        zlevel: 0,
        rippleEffect: {
          brushType: 'stroke'
        },
        label: {
          normal: {
            show: true,
            position: 'right',
            formatter: '{b}'
          }
        },
        symbolSize(val) {
          return val[2] / 4
        },
        itemStyle: {
          normal: {
            color: '#B39DDB'
          }
        },
        data: [{
          name : this.des,
          value: _.concat(this.getCoords(this.des), 100)
        }]
      }]

      //** direct path */
      const el = _.find(this.route, n => !_.size(n.h))
      if(el) {
        series.push({
          name: this.des,
          type: 'lines',
          zlevel: 1,
          effect,
          lineStyle: {
            normal: {
              color: '#B39DDB',
              width: 0,
              curveness: 0
            }
          },
          data: convertData(this.des)
        }, {
          name: this.des,
          type: 'lines',
          zlevel: 2,
          symbol: ['none', 'arrow'],
          symbolSize: 10,
          effect: {
            show: true,
            period: 6,
            trailLength: 0,
            symbol: planePath,
            symbolSize: 15
          },
          lineStyle: {
            normal: {
              color: '#B39DDB',
              width: 1,
              opacity: 0.6,
              curveness: 0
            }
          },
          data: convertData(this.des)
        })
      }

      _.each(this.route, (n, i) => {
        _.each(n.h, (o, j) => {
          series.push({
            name: o,
            type: 'lines',
            zlevel: 1,
            effect,
            lineStyle: {
              normal: {
                color: color[i],
                width: 0,
                curveness: 0
              }
            },
            data: convertData(o, !j ? false : n.h[j - 1])
          }, {
            name: o,
            type: 'lines',
            zlevel: 2,
            symbol: ['none', 'arrow'],
            symbolSize: 10,
            effect: {
              show: true,
              period: 6,
              trailLength: 0,
              symbol: planePath,
              symbolSize: 15
            },
            lineStyle: {
              normal: {
                color: color[i],
                width: 1,
                opacity: 0.6,
                curveness: 0
              }
            },
            data: convertData(o, !j ? false : n.h[j - 1])
          }, {
            name: o,
            type: 'effectScatter',
            coordinateSystem: 'geo',
            zlevel: 0,
            rippleEffect: {
              brushType: 'stroke'
            },
            label: {
              normal: {
                show: true,
                position: 'right',
                formatter: '{b}'
              }
            },
            symbolSize(val) {
              return val[2] / 8
            },
            itemStyle: {
              normal: {
                color: color[i],
              }
            },
            data: [{
              name : o,
              value: _.concat(this.getCoords(o), 100)
            }]
          })
        })

        series.push({
          name: this.des,
          type: 'lines',
          zlevel: 1,
          effect,
          lineStyle: {
            normal: {
              color: color[i],
              width: 0,
              curveness: 0
            }
          },
          data: convertData(this.des, _.last(n.h))
        }, {
          name: this.des,
          type: 'lines',
          zlevel: 2,
          symbol: ['none', 'arrow'],
          symbolSize: 10,
          effect: {
            show: true,
            period: 6,
            trailLength: 0,
            symbol: planePath,
            symbolSize: 15
          },
          lineStyle: {
            normal: {
              color: color[i],
              width: 1,
              opacity: 0.6,
              curveness: 0
            }
          },
          data: convertData(this.des, _.last(n.h))
        })
      })

      const opt = {
        backgroundColor: '#404a59',
        tooltip : {
          trigger: 'item',
        },
        legend: {
          orient: 'vertical',
          top: 'bottom',
          left: 'right',
          data: _.map(this.route, n => n.h[0] || this.des),
          textStyle: {
            color: '#fff',
            selectedMode: 'single',
          },
        },
        geo: {
          map: 'usa',
          label: {
            emphasis: {
              show: false,
            },
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#323c48',
              borderColor: '#FEFEFE',
            },
            emphasis: {
              areaColor: '#2a333d',
            }
          }
        },
        series: series
      }

      this.chart.setOption(opt)

      window.addEventListener('resize', this.chart.resize)

    },
    getXY(x, y) {
      const [a, b] = [_.find(this.airport, n => n.code === x), _.find(this.airport, n => n.code === y)]
      return {
        fromName: a.code,
        toName  : b.code,
        coords  : [[a.lon, a.lat], [b.lon, b.lat]],
      }
    },
    getCoords(x) {
      const o = _.find(this.airport, n => n.code === x)
      return [o.lon, o.lat]
    },
  },
  watch: {
    route(to, from) {
      _.delay(this.drawChart, 300)
    },
  },
  created() {

},
  mounted() {
    echarts.registerMap('usa', usa)
    _.delay(this.drawChart, 1)
  }
}
</script>

<style scoped lang='stylus'>
#echartsWrapper
  width: 100%
  height: calc(100vh - 64px)
</style>
